import "./yahtzeeTable.css";
import { YAHTZEE_TYPE } from "./yahtzeeUtil";

const upperRows = [
    { name: "Aces(1)", type: YAHTZEE_TYPE.ACES },
    { name: "Twos(2)", type: YAHTZEE_TYPE.TWOS },
    { name: "Threes(3)", type: YAHTZEE_TYPE.THREES },
    { name: "Fours(4)", type: YAHTZEE_TYPE.FOURS },
    { name: "Fives(5)", type: YAHTZEE_TYPE.FIVES },
    { name: "Sixes(6)", type: YAHTZEE_TYPE.SIXS },
];

const lowerRows = [
    { name: "3 of a Kind ()", type: YAHTZEE_TYPE.THREE_OF_A_KIND },
    { name: "4 of a Kind ()", type: YAHTZEE_TYPE.FOUR_OF_A_KIND },
    { name: "Full House ()", type: YAHTZEE_TYPE.FULL_HOUSE },
    { name: "Small Straight ()", type: YAHTZEE_TYPE.SMALL_STRAIGHT },
    { name: "Large Straight ()", type: YAHTZEE_TYPE.LARGE_STRAIGHT },
    { name: "YAHTZEE ()", type: YAHTZEE_TYPE.YAHTZEE },
    { name: "Chance ()", type: YAHTZEE_TYPE.CHANCE }
]

export const YahtzeeTable = (props: { combinations: Map<YAHTZEE_TYPE, number>, onSelectRow: ((key:YAHTZEE_TYPE) => void) | null }) => {
    const getCombinationValue = (type: YAHTZEE_TYPE) => {
        return props.combinations.get(type)! === -Infinity ? 0 : props.combinations.get(type);
    }
    const getValue = (type: YAHTZEE_TYPE) => {
        return props.combinations.get(type)!;
    }
    const getDisplay = (type: YAHTZEE_TYPE) => {
        return (props.onSelectRow || (!props.onSelectRow && getValue(type)! < 0)) && getCombinationValue(type) && Math.abs(getCombinationValue(type)!);
    }
    const onClick = (type: YAHTZEE_TYPE) => {
        return props.onSelectRow ? props.onSelectRow!(type) : undefined
    }
    return (
        <div className="yahtzee-table">
            <table>
                <tbody>
                    { upperRows.map(row => (
                        <tr key={ row.type }>
                            <th>{ row.name }</th>
                            <td className={ `yahtzee-table-${props.onSelectRow && getValue(row.type) >= 0 ? "highlight" : getValue(row.type) < 0 ? "fixed" : "cell"}` } onClick={ () => onClick(row.type) }>{ getDisplay(row.type) }</td>
                        </tr>
                    ))}
                    <tr>
                        <th>Bonus (63+)  (63+)</th>
                        <td>{ Math.abs(getDisplay(YAHTZEE_TYPE.YAHTZEE_BONUS) as number) || 0 }</td>
                    </tr>
                    <tr>
                        <th>Total of Upper Section ()</th>
                        <td>{ Array.from(props.combinations).reduce((prev, currPair) => prev + (currPair[0] < 10 && currPair[1] < 0 ? Math.abs(getDisplay(currPair[0]) || 0) : 0), 0) }</td>
                    </tr>

                </tbody>
            </table>
            <table>
                <tbody>
                    { lowerRows.map(row => (
                        <tr key={ row.type }>
                            <th>{ row.name }</th>
                            <td className={ `yahtzee-table-${props.onSelectRow && getValue(row.type) >= 0 ? "highlight" : getValue(row.type) < 0 ? "fixed" : "cell"}` } onClick={ () => onClick(row.type) }>{ getDisplay(row.type) }</td>
                        </tr>
                    ))}
                    <tr>
                        <th>Total of Lower Section ()</th>
                        <td>{ Array.from(props.combinations).reduce((prev, currPair) => prev + (currPair[0] > 10 && currPair[1] < 0 ? Math.abs(getDisplay(currPair[0]) || 0) : 0), 0) }</td>
                    </tr>
                </tbody>
            </table>
            <table>
                <tbody>
                    <tr>
                        <th>Total ()</th>
                        <td>{ Array.from(props.combinations).reduce((prev, currPair) => prev + (currPair[1] < 0 ? Math.abs(getDisplay(currPair[0]) || 0) : 0), 0) }</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}
